const getTextMesh = (text, x, y, z, rx, ry, rz, size, height, curveSegments, fontLoader, font) =>
{
    const textGeometry = new TextGeometry(
        text,
        {
            font: fontLoader.parse(font),
            size: size,
            height: height,
            curveSegments: curveSegments
        }
    )
    textGeometry.computeBoundingBox()
    textGeometry.translate(
        - textGeometry.boundingBox.max.x * 0.5,
        - textGeometry.boundingBox.max.y * 0.5,
        - textGeometry.boundingBox.max.z * 0.5
    )
    textGeometry.center()

    const textMaterial = new THREE.MeshBasicMaterial()
    const textMesh = new THREE.Mesh(textGeometry, textMaterial)
    textMesh.position.x = x
    textMesh.position.y = y
    textMesh.position.z = z
    textMesh.rotation.x = rx
    textMesh.rotation.y = ry
    textMesh.rotation.z = rz

    return textMesh
}

// Raycast logic
let raycast_enabled = true
const cameraRaycastBoxes = []
const raycastMap = new Map()
const cameraRaycastBox = (lx, ly, x, y, z, rx, ry, rz, visible, cx, cy, cz, tx, ty, tz) =>
{
    const geometry = new THREE.BoxGeometry( lx, ly, .005 );
    const material = new THREE.MeshBasicMaterial( {color: 0xFFFFFF} );
    const cube = new THREE.Mesh( geometry, material );
    cube.position.x = x
    cube.position.y = y
    cube.position.z = z
    cube.rotation.x = rx
    cube.rotation.y = ry
    cube.rotation.z = rz

    cube.visible = visible
    scene.add(cube)
    cameraRaycastBoxes.push(cube)
    raycastMap.set(cube.uuid, {cx, cy, cz, tx, ty, tz})
}

cameraRaycastBox(2, 1, 0, 2.7, 1.15, 0, 0, 0, false, 0.0903, 3.7705, 4.2598, 0.0632, 3.3024, .3730)


const raycaster = new THREE.Raycaster()
const mouse = new THREE.Vector2()
const oldMouse = new THREE.Vector2()
let oldTime = 0

let prev_camera_position = (new Vector3()).copy(camera_start)
let prev_target_position = (new Vector3()).copy(target_start)

window.addEventListener('mousedown', () => 
{
    oldMouse.x = event.clientX/ sizes.width * 2 - 1
    oldMouse.y = - (event.clientY / sizes.height * 2 - 1)
    oldTime = clock.getElapsedTime()
})

window.addEventListener('click', () => 
{
    mouse.x = event.clientX/ sizes.width * 2 - 1
    mouse.y = - (event.clientY / sizes.height * 2 - 1)
    raycaster.setFromCamera(mouse, camera)
    const intersect = raycaster.intersectObjects(cameraRaycastBoxes)[0]

    const max_secs_elapsed = .4
    const dsqr_minus_one_to_one = .0001
    if (intersect && raycast_enabled && mouse.distanceToSquared(oldMouse) < dsqr_minus_one_to_one && clock.getElapsedTime() - oldTime < max_secs_elapsed){
        prev_camera_position.copy(camera.position)
        prev_target_position.copy(controls.target)

        const camInfo = raycastMap.get(intersect.object.uuid)

        gsap.to(camera.position, {
            x: camInfo['cx'],
            y: camInfo['cy'],
            z: camInfo['cz'],
            duration: 1
        })

        gsap.to(controls.target, {
            x: camInfo['tx'],
            y: camInfo['ty'],
            z: camInfo['tz'],
            duration: 1
        })

        controls.enabled = false
        raycast_enabled = false
    }
})

const backButton = document.getElementById("backButton")
backButton.addEventListener('click', () => 
{
    gsap.to(camera.position, {
        x: prev_camera_position.x,
        y: prev_camera_position.y,
        z: prev_camera_position.z,
        duration: 1
    })

    gsap.to(controls.target, {
        x: prev_target_position.x,
        y: prev_target_position.y,
        z: prev_target_position.z,
        duration: 1
    })

    controls.enabled = true
    raycast_enabled = true
})

